<template>
  <gt-login :ds="dataSource"
    v-bind="config"></gt-login>
</template>

<script>
import GtLogin from '@/components/gt-login/index.vue';
import * as types from '@/store/types/account-types.js';
import md5 from 'md5';

export default {
  components: {
    GtLogin,
  },
  data () {
    return {
      dataSource: {
        login: this.login,
        getVcode: this.getVcode,
      },
      config: {
        register: false,
      },
    };
  },

  methods: {
    login (formData) {
      const {
        username,
        password,
        pid,
        code,
      } = formData;
      return this.$store.dispatch(types.LOGIN_REQUEST, {
        username,
        password: md5(`${password}${username}`),
        platform: 'cgtdesk',
        dog_info: {},
        pid,
        code,
      }).then(res => {
        this.$router.push('/about');
        return res;
      });
    },
    getVcode () {
      return this.$store.dispatch(types.GET_VCODE_REQUEST).then(res => {
        return {
          pid: res.data.pid,
          img: res.data.img,
        };
      }, () => {
        this.$message({
          message: '验证码服务暂时不可用',
          type: 'info',
        });
        this.config = {
          ...this.config,
          vcode: false,
        };
      });
    },
  },
};
</script>
